.headerPicWrapper {
  position: relative;
  overflow: hidden; }
  .headerPicWrapper:hover .input {
    display: block; }

.bgImg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1; }

.children {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 3; }

.input {
  position: absolute;
  outline: none;
  width: 0px;
  height: 0px;
  right: 0;
  top: 0;
  z-index: 22;
  display: none; }
  .input::after {
    content: 'Change cover';
    display: block;
    white-space: nowrap;
    position: absolute;
    color: rgba(255, 255, 255, 0.8);
    line-break: unset;
    padding: 8px 12px;
    font-size: 18px;
    right: 20px;
    top: 20px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.5); }



